  import StrictlyGoods from '@/components/common/StrictlyGoods'
  import AdvertisingPosition from '@/components/common/AdvertisingPosition'
  import T from '@/utils/tips.js'
  export default {
    name: "success",
    data() {
      return {
        query: '', // 获取传递参数
        adPositione: '', // 获取广告位信息
		orderId:'',
		payPrice:''
      }
    },
    components: {
      StrictlyGoods,
      AdvertisingPosition
    },
    onLoad(options) {
      this.orderId = options.orderId
	  this.payPrice = options.payPrice
    },
    methods: {
      // 广告位url跳转
      goPath(url) {
        if (url) {
          location.href = url
        }
      },
      // 查看订单详情
      checkOrderDetal() {
        // 详情需要 orderId, shopId
		uni.navigateTo({
			url:'/pages/user/order/detail?orderId=' + this.orderId
		})
      },
      // 返回首页
      goHome() {
		  uni.switchTab({
		  	url:'/pages/main/main'
		  })
      }
    }
  }
